// lib/utils.ts

import type { CalendarDay, Schedule } from "./types";

/**
 * Êúà„ÅÆ„Ç´„É¨„É≥„ÉÄ„Éº„Éá„Éº„Çø„ÇíÁîüÊàê
 */
export function generateCalendarDays(
  year: number,
  month: number,
  schedules: Schedule[]
): CalendarDay[] {
  const days: CalendarDay[] = [];
  const firstDay = new Date(year, month - 1, 1);
  const lastDay = new Date(year, month, 0);
  const today = new Date();
  today.setHours(0, 0, 0, 0);

  // Êúà„ÅÆÊúÄÂàù„ÅÆÈÄ±„ÅÆÂâç„ÅÆÊúà„ÅÆÊó•‰ªò„ÇíËøΩÂä†
  const firstDayOfWeek = firstDay.getDay();
  const prevMonthLastDay = new Date(year, month - 1, 0).getDate();

  for (let i = firstDayOfWeek - 1; i >= 0; i--) {
    const date = new Date(year, month - 2, prevMonthLastDay - i);
    days.push({
      date,
      isCurrentMonth: false,
      isToday: false,
      schedules: getSchedulesForDate(date, schedules),
    });
  }

  // ÂΩìÊúà„ÅÆÊó•‰ªò„ÇíËøΩÂä†
  for (let day = 1; day <= lastDay.getDate(); day++) {
    const date = new Date(year, month - 1, day);
    const dateOnly = new Date(date);
    dateOnly.setHours(0, 0, 0, 0);

    days.push({
      date,
      isCurrentMonth: true,
      isToday: dateOnly.getTime() === today.getTime(),
      schedules: getSchedulesForDate(date, schedules),
    });
  }

  // Êúà„ÅÆÊúÄÂæå„ÅÆÈÄ±„ÅÆÊ¨°„ÅÆÊúà„ÅÆÊó•‰ªò„ÇíËøΩÂä†
  const remainingDays = 7 - (days.length % 7);
  if (remainingDays < 7) {
    for (let day = 1; day <= remainingDays; day++) {
      const date = new Date(year, month, day);
      days.push({
        date,
        isCurrentMonth: false,
        isToday: false,
        schedules: getSchedulesForDate(date, schedules),
      });
    }
  }

  return days;
}

/**
 * ÊåáÂÆöÊó•‰ªò„ÅÆ‰∫àÂÆö„ÇíÂèñÂæó
 */
export function getSchedulesForDate(
  date: Date,
  schedules: Schedule[]
): Schedule[] {
  const dateStr = formatDate(date);
  return schedules
    .filter((schedule) => schedule.date === dateStr)
    .sort((a, b) => a.startTime.localeCompare(b.startTime));
}

/**
 * Êó•‰ªò„ÇíYYYY-MM-DDÂΩ¢Âºè„Å´„Éï„Ç©„Éº„Éû„ÉÉ„Éà
 */
export function formatDate(date: Date): string {
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, "0");
  const day = String(date.getDate()).padStart(2, "0");
  return `${year}-${month}-${day}`;
}

/**
 * Êó•‰ªòÊñáÂ≠óÂàó„ÇíDate„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„Å´Â§âÊèõ
 */
export function parseDate(dateStr: string): Date {
  const [year, month, day] = dateStr.split("-").map(Number);
  return new Date(year, month - 1, day);
}

/**
 * ÊôÇÈñì„ÇíHH:mmÂΩ¢Âºè„Å´„Éï„Ç©„Éº„Éû„ÉÉ„Éà
 */
export function formatTime(date: Date): string {
  const hours = String(date.getHours()).padStart(2, "0");
  const minutes = String(date.getMinutes()).padStart(2, "0");
  return `${hours}:${minutes}`;
}

/**
 * Êó•‰ªò„Å®ÊôÇÈñì„ÇíÁµêÂêà„Åó„Å¶Date„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„Çí‰ΩúÊàê
 */
export function combineDateAndTime(dateStr: string, timeStr: string): Date {
  const [year, month, day] = dateStr.split("-").map(Number);
  const [hours, minutes] = timeStr.split(":").map(Number);
  return new Date(year, month - 1, day, hours, minutes);
}

