/**
 * HEX„Ç´„É©„Éº„ÅÆÊòéÂ∫¶„ÇíÂà§ÂÆöÔºà„ÉÜ„Ç≠„Çπ„ÉàËâ≤„ÇíÊ±∫ÂÆö„Åô„Çã„Åü„ÇÅÔºâ
 */
export function isLightColor(hexColor: string): boolean {
  const hex = hexColor.replace("#", "");
  const r = parseInt(hex.substr(0, 2), 16);
  const g = parseInt(hex.substr(2, 2), 16);
  const b = parseInt(hex.substr(4, 2), 16);
  const brightness = (r * 299 + g * 587 + b * 114) / 1000;
  return brightness > 155;
}

/**
 * „Éï„Ç°„Ç§„É´„ÇíBase64„Å´Â§âÊèõ
 */
export function fileToBase64(file: File): Promise<string> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      if (typeof reader.result === "string") {
        resolve(reader.result);
      } else {
        reject(new Error("Failed to convert file to base64"));
      }
    };
    reader.onerror = (error) => reject(error);
  });
}

/**
 * Base64„Åã„ÇâmimeType„ÇíÊäΩÂá∫
 */
export function getMimeTypeFromBase64(base64: string): string {
  const match = base64.match(/data:([^;]+);/);
  return match ? match[1] : "image/jpeg";
}

/**
 * Base64„Åã„Çâ„Éá„Éº„ÇøÈÉ®ÂàÜ„ÅÆ„Åø„ÇíÊäΩÂá∫
 */
export function getBase64Data(base64: string): string {
  return base64.split(",")[1] || base64;
}

/**
 * „Éê„É™„Éá„Éº„Ç∑„Éß„É≥: Êó•‰ªò„ÅåÊúâÂäπ„Åã
 */
export function isValidDate(dateStr: string): boolean {
  const regex = /^\d{4}-\d{2}-\d{2}$/;
  if (!regex.test(dateStr)) return false;

  const date = parseDate(dateStr);
  return !isNaN(date.getTime());
}

/**
 * „Éê„É™„Éá„Éº„Ç∑„Éß„É≥: ÊôÇÈñì„ÅåÊúâÂäπ„Åã
 */
export function isValidTime(timeStr: string): boolean {
  const regex = /^\d{2}:\d{2}$/;
  if (!regex.test(timeStr)) return false;

  const [hours, minutes] = timeStr.split(":").map(Number);
  return hours >= 0 && hours < 24 && minutes >= 0 && minutes < 60;
}

/**
 * „Éê„É™„Éá„Éº„Ç∑„Éß„É≥: ÁµÇ‰∫ÜÊôÇÈñì„ÅåÈñãÂßãÊôÇÈñì„Çà„ÇäÂæå„Åã
 */
export function isValidTimeRange(startTime: string, endTime: string): boolean {
  return startTime < endTime;
}
