// components/ImageUpload/ImageUpload.tsx

"use client";

import { useState, useRef } from "react";
import { useRouter } from "next/navigation";
import type { OCRResult, Category, ScheduleInput } from "@/lib/types";
import { CATEGORIES } from "@/lib/types";
import { formatDate } from "@/lib/utils";
import styles from "./ImageUpload.module.css";

export default function ImageUpload() {
  const router = useRouter();
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [previewUrl, setPreviewUrl] = useState<string | null>(null);
  const [personName, setPersonName] = useState<string>("");
  const [dragOver, setDragOver] = useState(false);
  const [loading, setLoading] = useState(false);
  const [ocrResults, setOcrResults] = useState<OCRResult[]>([]);
  const [editedResults, setEditedResults] = useState<ScheduleInput[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);

  const handleFileSelect = (file: File) => {
    if (!file.type.startsWith("image/")) {
      alert("ÁîªÂÉè„Éï„Ç°„Ç§„É´„ÇíÈÅ∏Êäû„Åó„Å¶„Åè„Å†„Åï„ÅÑ");
      return;
    }

    setSelectedFile(file);
    setError(null);
    setSuccess(false);

    const reader = new FileReader();
    reader.onload = (e) => {
      setPreviewUrl(e.target?.result as string);
    };
    reader.readAsDataURL(file);
  };

  const handleFileInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      handleFileSelect(file);
    }
  };

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setDragOver(true);
  };

  const handleDragLeave = () => {
    setDragOver(false);
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    setDragOver(false);

    const file = e.dataTransfer.files[0];
    if (file) {
      handleFileSelect(file);
    }
  };

  const handleUploadClick = () => {
    fileInputRef.current?.click();
  };

  const handleAnalyze = async () => {
    if (!selectedFile || !previewUrl) return;

    try {
      setLoading(true);
      setError(null);

      const formData = new FormData();
      formData.append("image", previewUrl);
      formData.append("name", personName || "");

      const response = await fetch("/api/ocr", {
        method: "POST",
        body: formData,
      });

      const data = await response.json();

      if (data.success && data.data) {
        setOcrResults(data.data);

        // OCRÁµêÊûú„ÇíÁ∑®ÈõÜÂèØËÉΩ„Å™ÂΩ¢Âºè„Å´Â§âÊèõ
        const today = formatDate(new Date());
        const schedules: ScheduleInput[] = data.data.map(
          (result: OCRResult) => {
            const category = CATEGORIES.find(
              (c) => c.value === result.category
            );
            return {
              date: result.date || today,
              startTime: result.startTime || "09:00",
              endTime: result.endTime || "10:00",
              title: result.title,
              description: result.description || "",
              category: result.category || "other",
              color: category?.defaultColor || CATEGORIES[3].defaultColor,
              completed: false,
            };
          }
        );

        // „ÇØ„É©„Ç§„Ç¢„É≥„ÉàÂÅ¥„Åß„ÇÇÊåáÂÆöÂêç„Åß„Éï„Ç£„É´„Çø„Çí„Åã„Åë„ÇãÔºà„Çµ„Éº„Éê„Éº„ÅåËøî„Åï„Å™„Åã„Å£„ÅüÂ†¥Âêà„ÅÆ„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÔºâ
        const normalize = (s?: string) =>
          (s || "")
            .normalize("NFKC")
            .toLowerCase()
            .replace(/\s+/g, "")
            .replace(/[\p{P}\p{S}]/gu, "");

        const levenshtein = (a: string, b: string) => {
          const dp: number[][] = Array.from({ length: a.length + 1 }, () =>
            new Array(b.length + 1).fill(0)
          );
          for (let i = 0; i <= a.length; i++) dp[i][0] = i;
          for (let j = 0; j <= b.length; j++) dp[0][j] = j;
          for (let i = 1; i <= a.length; i++) {
            for (let j = 1; j <= b.length; j++) {
              const cost = a[i - 1] === b[j - 1] ? 0 : 1;
              dp[i][j] = Math.min(
                dp[i - 1][j] + 1,
                dp[i][j - 1] + 1,
                dp[i - 1][j - 1] + cost
              );
            }
          }
          return dp[a.length][b.length];
        };

        let filteredSchedules = schedules;
        if (personName && personName.trim()) {
          const nm = normalize(personName.trim());
          const matchesName = (result: ScheduleInput, raw?: OCRResult) => {
            // try assignedTo/rawText if available in OCR results
            const ocr = raw as OCRResult | undefined;
            if (ocr?.assignedTo && ocr.assignedTo.length > 0) {
              for (const p of ocr.assignedTo) {
                const pn = normalize(p);
                if (!pn) continue;
                if (pn.includes(nm) || nm.includes(pn)) return true;
                const maxDist = Math.max(
                  1,
                  Math.floor(Math.max(nm.length, pn.length) * 0.3)
                );
                if (levenshtein(pn, nm) <= maxDist) return true;
              }
              return false;
            }

            const fields = [
              result.title,
              result.description,
              (ocr && ocr.rawText) || "",
            ]
              .filter(Boolean)
              .map((s) => normalize(String(s)));
            for (const f of fields) {
              if (f.includes(nm) || nm.includes(f)) return true;
              if (
                levenshtein(f, nm) <= Math.max(1, Math.floor(nm.length * 0.25))
              )
                return true;
            }
            return false;
          };

          // map back OCR results to schedule inputs to filter with raw data if present
          filteredSchedules = schedules.filter((s, idx) =>
            matchesName(s, data.data[idx])
          );
        }

        setEditedResults(filteredSchedules);

        // Ëá™Âãï‰øùÂ≠òÔºöË©≤ÂΩìÂêç„ÅÆ‰∫àÂÆö„ÅåË¶ã„Å§„Åã„Å£„Åü„ÇâÂç≥‰øùÂ≠ò„Åó„Å¶„Ç´„É¨„É≥„ÉÄ„Éº„Å∏ÈÅ∑Áßª
        if (filteredSchedules.length > 0) {
          try {
            setLoading(true);
            const resp = await fetch("/api/schedules", {
              method: "POST",
              headers: { "Content-Type": "application/json" },
              body: JSON.stringify(filteredSchedules),
            });
            const resData = await resp.json();
            if (resData.success) {
              // ÁßªÂãïÂÖà„ÅØÊúÄÂàù„ÅÆ‰∫àÂÆö„ÅÆÊó•‰ªò
              const targetDate = filteredSchedules[0].date;
              // „É´„Éº„Éà„Å´ focus „ÇØ„Ç®„É™„ÅßÁßªÂãï
              router.push(`/?focus=${encodeURIComponent(targetDate)}`);
              router.refresh();
              return;
            } else {
              setError(resData.error || "Ëá™Âãï‰øùÂ≠ò„Å´Â§±Êïó„Åó„Åæ„Åó„Åü");
            }
          } catch (err) {
            console.error("Auto-save error:", err);
            setError("Ëá™Âãï‰øùÂ≠ò‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü");
          } finally {
            setLoading(false);
          }
        }
      } else {
        setError(data.error || "‰∫àÂÆö„ÅÆÊäΩÂá∫„Å´Â§±Êïó„Åó„Åæ„Åó„Åü");
      }
    } catch (err) {
      console.error("OCR error:", err);
      setError("OCRÂá¶ÁêÜ‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü");
    } finally {
      setLoading(false);
    }
  };

  // If no name provided, require user to input before analyzing
  const canAnalyze = !!personName.trim() && !!selectedFile && !!previewUrl;

  const handleResultChange = (
    index: number,
    field: keyof ScheduleInput,
    value: string | boolean
  ) => {
    setEditedResults((prev) => {
      const updated = [...prev];
      updated[index] = { ...updated[index], [field]: value };

      // „Ç´„ÉÜ„Ç¥„É™„ÉºÂ§âÊõ¥ÊôÇ„Å´Ëâ≤„ÇÇÊõ¥Êñ∞
      if (field === "category") {
        const category = CATEGORIES.find((c) => c.value === value);
        if (category) {
          updated[index].color = category.defaultColor;
        }
      }

      return updated;
    });
  };

  const handleSave = async () => {
    if (editedResults.length === 0) return;

    // „Éê„É™„Éá„Éº„Ç∑„Éß„É≥
    const hasError = editedResults.some(
      (result) =>
        !result.title.trim() ||
        !result.date ||
        !result.startTime ||
        !result.endTime
    );

    if (hasError) {
      alert("„Åô„Åπ„Å¶„ÅÆÂøÖÈ†àÈ†ÖÁõÆ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ");
      return;
    }

    try {
      setLoading(true);
      setError(null);

      const response = await fetch("/api/schedules", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(editedResults),
      });

      const data = await response.json();

      if (data.success) {
        setSuccess(true);
        setTimeout(() => {
          router.push("/schedules");
          router.refresh();
        }, 1500);
      } else {
        setError(data.error || "‰øùÂ≠ò„Å´Â§±Êïó„Åó„Åæ„Åó„Åü");
      }
    } catch (err) {
      console.error("Save error:", err);
      setError("‰øùÂ≠ò‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü");
    } finally {
      setLoading(false);
    }
  };

  const handleReset = () => {
    setSelectedFile(null);
    setPreviewUrl(null);
    setOcrResults([]);
    setEditedResults([]);
    setError(null);
    setSuccess(false);
    if (fileInputRef.current) {
      fileInputRef.current.value = "";
    }
  };

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>ÁîªÂÉè„Åã„Çâ‰∫àÂÆö„ÇíË™≠„ÅøËæº„ÇÄ</h1>

      {!selectedFile && (
        <>
          <div
            className={`${styles.uploadArea} ${
              dragOver ? styles.dragOver : ""
            }`}
            onClick={handleUploadClick}
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDrop={handleDrop}
          >
            <div className={styles.uploadIcon}>üì∑</div>
            <div className={styles.uploadText}>
              „ÇØ„É™„ÉÉ„ÇØ„Åæ„Åü„ÅØ„Éâ„É©„ÉÉ„Ç∞ÔºÜ„Éâ„É≠„ÉÉ„Éó„ÅßÁîªÂÉè„Çí„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ
            </div>
            <div className={styles.uploadHint}>
              „Ç´„É¨„É≥„ÉÄ„Éº„ÇÑÊâãÂ∏≥„ÅÆÂÜôÁúü„Åã„Çâ‰∫àÂÆö„ÇíËá™ÂãïÊäΩÂá∫„Åó„Åæ„Åô
            </div>
          </div>
          <input
            ref={fileInputRef}
            type="file"
            accept="image/*"
            onChange={handleFileInputChange}
            className={styles.fileInput}
          />
        </>
      )}

      {/* ÂêçÂâç„ÇíÂÖà„Å´ÂÖ•Âäõ„Åó„Å¶„ÇÇ„Çâ„ÅÜ */}
      <div style={{ marginTop: 12 }}>
        <label style={{ display: "block", marginBottom: 6 }}>
          ÂêçÂâçÔºàÊäΩÂá∫ÂØæË±°Ôºâ
        </label>
        <input
          type="text"
          value={personName}
          onChange={(e) => setPersonName(e.target.value)}
          placeholder="‰æã: Áî∞‰∏≠ Â§™ÈÉé"
          style={{ padding: 8, width: "100%", maxWidth: 360 }}
        />
      </div>

      {selectedFile && previewUrl && !loading && editedResults.length === 0 && (
        <div className={styles.preview}>
          <img src={previewUrl} alt="Preview" className={styles.previewImage} />
          <div className={styles.previewActions}>
            <button
              className={`${styles.button} ${styles.buttonSecondary}`}
              onClick={handleReset}
            >
              Âà•„ÅÆÁîªÂÉè„ÇíÈÅ∏Êäû
            </button>
            <button
              className={`${styles.button} ${styles.buttonPrimary}`}
              onClick={handleAnalyze}
              disabled={!canAnalyze}
              title={
                !canAnalyze
                  ? "ÁîªÂÉè„Å®ÊäΩÂá∫ÂØæË±°„ÅÆÂêçÂâç„ÇíÊåáÂÆö„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
                  : "‰∫àÂÆö„ÇíÊäΩÂá∫"
              }
            >
              ‰∫àÂÆö„ÇíÊäΩÂá∫
            </button>
          </div>
        </div>
      )}

      {loading && (
        <div className={styles.loading}>
          <div className={styles.loadingSpinner}></div>
          <div className={styles.loadingText}>
            {editedResults.length === 0 ? "ÁîªÂÉè„ÇíËß£Êûê‰∏≠..." : "‰∫àÂÆö„Çí‰øùÂ≠ò‰∏≠..."}
          </div>
        </div>
      )}

      {error && <div className={styles.error}>{error}</div>}

      {success && (
        <div className={styles.success}>
          ‰∫àÂÆö„Çí‰øùÂ≠ò„Åó„Åæ„Åó„ÅüÔºÅ‰∫àÂÆö‰∏ÄË¶ß„Éö„Éº„Ç∏„Å´ÁßªÂãï„Åó„Åæ„Åô...
        </div>
      )}

      {editedResults.length > 0 && !success && (
        <div className={styles.results}>
          <div className={styles.resultsHeader}>
            <h2 className={styles.resultsTitle}>ÊäΩÂá∫„Åï„Çå„Åü‰∫àÂÆö</h2>
            <p className={styles.resultsCount}>
              {editedResults.length}‰ª∂„ÅÆ‰∫àÂÆö„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åó„Åü
            </p>
          </div>

          <div className={styles.resultsList}>
            {editedResults.map((result, index) => {
              const category = CATEGORIES.find(
                (c) => c.value === result.category
              );
              return (
                <div key={index} className={styles.resultItem}>
                  <div className={styles.resultItemHeader}>
                    <input
                      type="text"
                      value={result.title}
                      onChange={(e) =>
                        handleResultChange(index, "title", e.target.value)
                      }
                      className={styles.resultItemTitle}
                      style={{ border: "none", outline: "none", width: "100%" }}
                    />
                    <span
                      className={styles.resultItemBadge}
                      style={{ backgroundColor: result.color }}
                    >
                      {category?.label}
                    </span>
                  </div>

                  <div className={styles.resultItemRow}>
                    <div className={styles.resultItemField}>
                      <label className={styles.resultItemLabel}>Êó•‰ªò</label>
                      <input
                        type="date"
                        value={result.date}
                        onChange={(e) =>
                          handleResultChange(index, "date", e.target.value)
                        }
                        className={styles.resultItemInput}
                      />
                    </div>

                    <div className={styles.resultItemField}>
                      <label className={styles.resultItemLabel}>ÈñãÂßãÊôÇÈñì</label>
                      <input
                        type="time"
                        value={result.startTime}
                        onChange={(e) =>
                          handleResultChange(index, "startTime", e.target.value)
                        }
                        className={styles.resultItemInput}
                      />
                    </div>

                    <div className={styles.resultItemField}>
                      <label className={styles.resultItemLabel}>ÁµÇ‰∫ÜÊôÇÈñì</label>
                      <input
                        type="time"
                        value={result.endTime}
                        onChange={(e) =>
                          handleResultChange(index, "endTime", e.target.value)
                        }
                        className={styles.resultItemInput}
                      />
                    </div>

                    <div className={styles.resultItemField}>
                      <label className={styles.resultItemLabel}>
                        „Ç´„ÉÜ„Ç¥„É™„Éº
                      </label>
                      <select
                        value={result.category}
                        onChange={(e) =>
                          handleResultChange(
                            index,
                            "category",
                            e.target.value as Category
                          )
                        }
                        className={styles.resultItemSelect}
                      >
                        {CATEGORIES.map((cat) => (
                          <option key={cat.value} value={cat.value}>
                            {cat.label}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>

                  <div className={styles.resultItemField}>
                    <label className={styles.resultItemLabel}>Ë©≥Á¥∞</label>
                    <textarea
                      value={result.description}
                      onChange={(e) =>
                        handleResultChange(index, "description", e.target.value)
                      }
                      className={styles.resultItemTextarea}
                      placeholder="‰∫àÂÆö„ÅÆË©≥Á¥∞..."
                    />
                  </div>
                </div>
              );
            })}
          </div>

          <div className={styles.saveButtons}>
            <button
              className={`${styles.button} ${styles.buttonSecondary}`}
              onClick={handleReset}
              disabled={loading}
            >
              „Ç≠„É£„É≥„Çª„É´
            </button>
            <button
              className={`${styles.button} ${styles.buttonPrimary}`}
              onClick={handleSave}
              disabled={loading}
            >
              „Åô„Åπ„Å¶‰øùÂ≠ò
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
